import React, { Component } from 'react';
import { withRouter } from 'react-router';
import { connect } from 'react-redux';
import Blogs from '@components/Blogs';
import NewCourse from '@components/NewCourse';
import DocumentSettings from '@utils/seo';

function BlogsScreen(props: { posts: Array<any> }) {
    const seo = {
        title: "Yazılarım | Can Gökçeaslan",
        description: "En son yazılarıma ulaşmak için hemen ziyaret edebilirsin",
        image: props.posts[0]?.image,
        keywords: "cangokceaslan, Blog, Son Yazılarım, Yazılım, Node.js, Http, Express, Can Gökçeaslan, Can Gokceaslan, CanGokceaslan, Node.js, NodeJs, JavaScript, Fullstack Developer, CoFounder, Girişim",
        author: "Can Gökçeaslan",
        locale: "tr",
        siteName: "https://www.cangokceaslan.com/blogs"
    }

    return (
        <React.Fragment>
            <DocumentSettings docData={seo} />
            <Blogs hideTitle={true} />
            <NewCourse />
        </React.Fragment>
    )
}

export default connect((state: { blogs: any }) => ({
    posts: state?.blogs?.posts,
    author: state?.blogs?.author,
    fetching: state?.blogs?.fetching
}), {
})(withRouter(BlogsScreen));